.tabs {
    display: flex;
    justify-content: space-between;

    .card-tab {
        padding: 10px;
        width: 25%;
        margin: 10px 15px;
        background: #FFFFFF;
        box-shadow: 0px 1px 4px rgba(0, 8, 32, 0.12), 0px 4px 8px rgba(0, 8, 32, 0.08);
        border-radius: 4px;
        cursor: pointer;

        p {
            margin: 0;
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-size: 16px;
            line-height: 26px;
        }

        .card-tab__title {
            font-weight: bold;
            color: #000000;
        }

        .card-tab__value {
            font-weight: normal;
            color: #556685;
        }
    }

    .select {
        p {
            color: #ffffff!important;
        }

        svg {
            stroke: #ffffff!important;
        }
    }


}

@media screen and (max-width: 1024px) {
    .tabs {
        display: block;

        .card-tab {
            width: auto!important;
        }
    }
}
