.checkbox-item {
    display: inline-flex;
    cursor: pointer;
    position: relative;
    margin: 5px 0;

    label {
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        color: #152540;
        margin-left: 12px;
        cursor: pointer;
        white-space: nowrap;
    }

    input {
        height: 18px;
        width: 18px;
        cursor: pointer;
    }

}
