.main-content {
    margin-bottom: 15px;
    position: relative;

    .category-list-img {
        margin-top: 10px;
        margin-bottom: 15px;
    }

    .content-list-title {
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 26px;
        color: #000000;
    }

    .main-content__list {
        display: flex;
        overflow: auto;
        padding: 5px 0;

        .carouel__container {
            width: 100%;
            position: unset!important;

            .react-multi-carousel-track  {
                position: unset!important;

                .carousel__item {
                    padding: 5px 0;
                }
            }
        }
    }

    .action-group {
        position: absolute;
        right: 0;
        top: 20px;

        div {
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 12px;
            line-height: 18px;
            letter-spacing: 0.008em;
            color: #556685;
            cursor: pointer;
            transition: all 0.3s;
        }

        div:hover {
            color: #2f3949;
            svg {
                stroke: #1d242e;
                transition: all 0.3s;
            }
        }

        .action-group__btn-previous {
            margin-right: 20px;
        }
    }
}


@media screen and (max-width: 744px) {
    .react-multi-carousel-track {
        li {
            display: flex;
            justify-content: center;
        }
    }
}
