.ReactModal__Overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent!important;
}

.detail-modal {
    width: 90%;
    margin: 0 5%;
    max-width: 740px;
    background-color: #ffffff;
    box-shadow: 0px 1px 4px rgba(0, 8, 32, 0.1), 0px 32px 64px rgba(0, 8, 32, 0.12);
    border-radius: 4px;
    padding: 50px;

    .detail-modal__close-btn {
        cursor: pointer;
        display: flex;
        justify-content: flex-end;
        margin-top: 0;
    }

    .detail-modal__content {
        display: flex;
        .detail-modal__image {
            width: 100%;
            img {
                width: 100%;
                // height: 100%;
            }
        }
        .detail-modal__item {
            padding-left: 30px;
            display: grid;

            .detail-modal__category {
                font-family: 'Poppins', sans-serif;
                font-style: normal;
                font-weight: bold;
                font-size: 14px;
                line-height: 15px;
                color: #5A55AB;
            }

            .detail-modal__author {
                font-family: 'Poppins', sans-serif;
                font-style: normal;
                font-weight: bold;
                font-size: 14px;
                line-height: 15px;
                color: #000000;
            }

            .detail-modal__title {
                font-family: 'Poppins', sans-serif;
                font-style: normal;
                font-weight: bold;
                font-size: 20px;
                line-height: 38px;
                color: #000000;
            }

            .detail-modal__text {
                font-family: 'Poppins', sans-serif;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 18px;
                color: #000000;
            }

            .modal-action {
                justify-content: flex-end;

                .modal-action__price {
                    font-family: 'Poppins', sans-serif;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 22px;
                    color: #128945;
                    margin-right: 20px;
                }

                .modal-action__visit {
                    background: #5A55AB;
                    border-radius: 100px;
                    border: none;
                    padding: 10px 15px;
                    color: #ffffff;
                    cursor: pointer;
                    transition: all 0.5s;
                }

                .modal-action__visit:hover {
                    background: #373375;
                }
            }
        }
    }
}

@media screen and (max-width: 664px) {

    .detail-modal {

        .detail-modal__content {
            display: grid!important;

            .detail-modal__image {
                margin-top: 5px;
                width: unset;
                display: flex;
                justify-content: center;

                img {
                    width: unset;
                    max-width: 120px;
                }
            }

            .detail-modal__item {
                padding-left: 0;
                padding-top: 5px;

                .detail-modal__author {
                    margin-bottom: 0;
                }

                .detail-modal__title {
                    line-height: 25px;
                    margin-bottom: 5px;
                }

                .detail-modal__text {
                    margin-top: 0;
                }
            }
        }
    }
}
