.result-card {
    height: 100%;
    margin: 0 10px;
    cursor: pointer;
    background: #FFFFFF;
    border-radius: 4px;
    display: grid;
    width: 170px;
    transition: all 0.3s;

    .result-card__img {
        display: flex;
        justify-content: center;
        padding : 17px 12px;

        img {
            height: 124px;
            filter: drop-shadow(0px 1px 4px rgba(0, 8, 32, 0.1)) drop-shadow(0px 4px 8px rgba(0, 8, 32, 0.24));
        }
    }

    .result-card__title {
        margin: 0 12px;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.008em;
        color: #000000;
        margin-bottom: 5px;
        min-height: 36px;
    }

    .result-card__author {
        margin: 0 12px;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 10px;
        line-height: 16px;
        color: #000000;
        margin-bottom: 10px;
    }


    .result-card__actions {
        display: flex;
        align-items: center;
        justify-content: center;
        border-top: 1px solid #D9E1EE;
        padding: 5px 12px;
        transition: all 0.3s;
        margin-bottom: 0;
        margin-top: auto;

        button {
            display: none;
            cursor: pointer;
            border-radius: 100px;
            border: 1px solid #5A55AB;
            color: #5A55AB;
            outline: none;
            padding: 5px 10px;
            background-color: #ffffff;
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 12px;
            line-height: 16px;
            text-align: center;
            letter-spacing: 0.008em;
            transition: all 0.3s;
        }

        button:hover {
            background-color: #c5c4e4;
        }

        .time-text {
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 12px;
            line-height: 16px;
            color: #8C88C4;
            margin-left: 5px;
        }
    }
}

.result-card:hover {
    box-shadow: 0px 1px 4px rgba(0, 8, 32, 0.12), 0px 4px 8px rgba(0, 8, 32, 0.08);

    .result-card__actions {
        justify-content: space-between;

        button {
            display: block;
        }
    }
}
