.recommend-card {
    height: 100%;
    margin: 0 15px;
    cursor: pointer;
    background: #FFFFFF;
    border-radius: 4px;
    display: grid;
    min-width: 255px;
    transition: all 0.3s;
    box-shadow: 0px 1px 4px rgba(0, 8, 32, 0.12), 0px 4px 8px rgba(0, 8, 32, 0.08);

    .recommend-card__img {
        width: 100%;

        img {
            width: 100%;
        }
    }

    .recommend-card__path {
        background: rgba(103, 194, 58, 0.1);
        border-radius: 2px;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 12px;
        color: #67C23A;
        width: fit-content;
        padding: 10px 5px;
        margin: 5px 15px
    }

    .recommend-card__title {
        margin: 10px 14px;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.008em;
        color: #000000;
        min-height: 36px;
    }

    .recommend-card__content {
        margin: 0 14px;
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        color: #8494B2;
        margin-bottom: 10px;
    }


    .recommend-card__actions {
        display: flex;
        align-items: center;
        justify-content: center;
        border-top: 1px solid #D9E1EE;
        padding: 5px 14px;
        transition: all 0.3s;
        margin-bottom: 0;
        margin-top: auto;

        .time-text {
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 12px;
            line-height: 16px;
            color: #8C88C4;
            margin-left: 5px;
        }
    }
}
